import { useEffect, useState } from "react";
import { onAuthStateChanged, User } from "firebase/auth";
import { auth } from "@/src/firebaseConfig";

/**
 * Custom hook ƒë·ªÉ l·∫•y th√¥ng tin user hi·ªán t·∫°i t·ª´ Firebase Authentication.
 * @returns { currentUser, loading } - currentUser: th√¥ng tin user, loading: tr·∫°ng th√°i ƒëang t·∫£i.
 */
export function useAuth() {
  const [currentUser, setCurrentUser] = useState<User | null>(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const unsubscribe = onAuthStateChanged(auth, (user) => {
      setCurrentUser(user);
      setLoading(false);
    });

    return () => unsubscribe(); // Clean up listener khi component unmount
  }, []);

  return { currentUser, loading };
}
